import React from 'react'

import { render, fireEvent, waitFor } from '@testing-library/react-native'

import { Address } from '../Address'

describe('Address component', () => {
  it('should copy full address to clipboard and change opacity on press', async () => {
    const rlyAccount = 'exampleAddress12345'

    const { getByTestId } = render(<Address rlyAccount={rlyAccount} />)

    const copyButton = getByTestId('copy-button')

    // Trigger the press event on the copy button
    fireEvent.press(copyButton)

    // Use waitFor to wait for the opacity change to take effect
    await waitFor(() => {
      expect(copyButton.props.style[0].opacity).toBe(0.5)
    })

    // Use waitFor to wait for the opacity to return to 1
    await waitFor(() => {
      expect(copyButton.props.style[0].opacity).toBe(1)
    })
  })
})
